import axios from '@/http/axios.ts';
import qs from 'qs';
function headers() { // json请求头封装
    return {
        headers: {
            'Content-Type': 'application/json',
        },
    };
}

function jsonSet(data: any) { // 转json格式
    return JSON.stringify(data);
}
function getNews(data: any) {
    return axios.get('/a/u/news', {
        params: data,
    });
}
function addNews(params: any) {            // 新增
    return axios.post('/a/u/news', jsonSet(params), headers());
}
function editNews(params: any) {     // 编辑
    return axios.put('/a/u/news', jsonSet(params), headers());
}
// 查询指定id的任务课程
function getNewsId(id: number) {              // 列表查看
    return axios.get(`/a/u/news/${id}`);
}
// 查询指定id的任务课程
function delNews(id: number) {              // 删除
    return axios.delete(`/a/u/news/${id}`);
}
export {
    getNews,
    addNews,
    editNews,
    getNewsId,
    delNews,
};


