<template>
  <main id="informatDetail">
     <Header :title="ifoDetails.title"></Header>
    <div class="content tal">
      <strong class="le-sp-10 mg-b-20" style="display:inline-block">{{ifoDetails.title}}</strong>
      <div class="font-aaa mg-b-20">
        <span style="margin-right: 20px;">{{ifoDetails.createAt | formatDate}}</span>
        <span class="mg-l-r-5p">阅读</span>
        <span>{{ifoDetails.readCount}}</span>
      </div>
      <img :src="ifoDetails.coverImg" alt :onerror="errImg" style="width:90%;margin:0 5% 20px 5%">
      <p class="tal" v-html="ifoDetails.content" style="text-indent:2em"></p>
    </div>
  </main>
</template>

<script>
import { getNewsId } from "@/http/content/news.ts";
import Header from "@/components/Header.vue";
export default {
  name: "informatDetail",
  data() {
    return {
      errImg: '',// 报错显示的图片
      ifoDetails: {},
      params: {},
      zxId: null,
    };
  },
  created() {
      this.zxId = this.$route.query.id;
      console.log(this.zxId);
      getNewsId(this.zxId)
      .then(res=>{
          console.log(res)
          this.ifoDetails = res.data.data;
      });
  },
  filters: {
      formatDate: function (value) {
        let date = new Date(value);
        let y = date.getFullYear();
        let MM = date.getMonth() + 1;
        MM = MM < 10 ? ('0' + MM) : MM;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let m = date.getMinutes();
        m = m < 10 ? ('0' + m) : m;
        let s = date.getSeconds();
        s = s < 10 ? ('0' + s) : s;
        return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
      }
    },
};
</script>

<style lang="scss" scoped>
#informatDetail .top-box {
  position: relative;
  display: flex;
  top: 0;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 7vh;
  background: #03a1e8;

  .return {
    position: absolute;
    top: 50%;
    left: 3%;
    width: 20px;
    height: 20px;
    margin-left: 1rem;
    transform: translateY(-50%);
  }
  .title {
    width: 8em;
    font-size: 20px;
    color: #ffffff;
    line-height: 7vh;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}
#informatDetail .content {
  padding: 3%;
  // text-indent: 2em;
}
</style>